<template>
    <div class="yingyong">
        <headerBar :title="headerNavData" @showaddresBook="showaddresBook()"></headerBar>
        <yd-flexbox class="yd-cell-item">
            <yd-flexbox-item>
              <yd-input style="width: 100%;height: 1rem;font-size: .28rem;padding-left: .12rem" type="text" v-model="codedata.pks"  :show-clear-icon="false" :show-success-icon="false" readonly=""></yd-input>
            </yd-flexbox-item>
        </yd-flexbox>
        <yd-flexbox class="yd-cell-item">
            <yd-flexbox-item>
              <yd-input style="width: 100%;height: 1rem;font-size: .28rem;padding-left: .12rem" type="text" v-model="codedata.addr" :show-clear-icon="false" :show-success-icon="false" :placeholder="$t('placeholder.codeaddress')"></yd-input>
            </yd-flexbox-item>
            <div><span  class="touxi"><img src="../assets/images/erweima.png" style="margin-right: .24rem" @click="scan()"></span></div>
        </yd-flexbox>
        <yd-cell-group>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="number" v-model="codedata.num" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('placeholder.codenum')"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
                <span slot="left"></span>
                <yd-input slot="right" type="text" v-model="codedata.beizhu" :show-clear-icon="false" :show-success-icon="false" :show-required-icon="false" :placeholder="$t('placeholder.codemome')"></yd-input>
            </yd-cell-item>
        </yd-cell-group>
        <div class="block">
          <yd-flexbox class="ttle">
            <yd-flexbox-item>
              {{$t('code.kgf')}}
            </yd-flexbox-item>
            <!-- <div class="wenhao"><img src="../assets/images/w.png"></div> -->
          </yd-flexbox>
          <el-slider v-model="num" :min="min" :max="max" :step="step" :show-tooltip="false"></el-slider>
          <yd-flexbox class="qml">
            <div class="man">{{$t('data.slow')}}</div>
            <yd-flexbox-item>
              <div class="kgf">{{num/10000 | DAD}} eth</div>
            </yd-flexbox-item>
            <div class="kuai">{{$t('data.fast')}}</div>
          </yd-flexbox>
          <button type="button" class="btn_c" @click="showpwd()">{{$t('code.zhuanzhang')}}</button>
        </div>
        <!-- 请输入密码 -->
        <yd-popup v-model="show1" position="center" width="90%">
            <div class="dig">
                <p class="ttl">
                    {{padtxt}}
                </p>
                <input type="password" name="" v-model="pawd">
                <p class="dig_bt">
                    <yd-button @click.native="show1 = false">{{$t('code.cancel')}}</yd-button>
                    <yd-button @click.native="trade()">{{$t('code.ok')}}</yd-button>
                </p>
            </div>
        </yd-popup>
        <!-- 我的地址列表 -->
        <div class="addressbooklist" v-if="showaddresBookbool">
          <yd-navbar title="地址簿" :bgcolor="headerNavData.bj" fontsize=".36rem" color="#ffffff" height=".9rem">
            <div slot="left" @click="showaddresBookbool=false">
                <yd-navbar-back-icon color="#ffffff"></yd-navbar-back-icon>
            </div>
        </yd-navbar>
          <div class="mysearch12">
            <yd-search v-model="search":on-cancel="cancelHandler"
              :placeholder="$t('placeholder.search')"></yd-search>
          </div>
          <div class="dresslist">
            <p class="select_p" @click="goaddbook"  v-if="friendlist.length==0">{{$t('data.addressbook')}}</p>
            <div class="tade_item" v-for="item in friendlist" @click="getItem(item)" v-if="friendlist.length>0">
              <yd-flexbox class="trade_info">
                  <yd-flexbox-item class="trade_addr">
                    <span class="left_d">{{item.Remark}}</span>
                  </yd-flexbox-item>
                  <div><span class="dian"></span></div>
              </yd-flexbox>
              <yd-flexbox class="trade_dabao">
                  <div><span class="zwf"></span></div>
                  <yd-flexbox-item class="succorfail">
                    <span>Address：{{item.Target | ordernum}}</span>
                    <span :class="{'choseact':codedata.addr==item.Target}"></span>
                  </yd-flexbox-item>
              </yd-flexbox>
            </div>
          </div>
        </div>
    </div>
  
</template>

<script>
import {
  getTokenQty,
  price,
  MysendRawTransaction,
  getPrivateFun,
  ToZhuanZhang,
  Setvuefghfvmdfgdfgdfg,
  Getvuefghfvmdfgdfgdfg,
  GetGasPrice,
  MysendRawTX,
  isAddressto
} from "../assets/js/Keytoken.js";
import { getCookie, setCookie } from "../assets/js/cookie.js";
import { toAddress } from "../assets/js/iban.js";
import headerBar from "./header.vue";
export default {
  components: {
    headerBar: headerBar
  },
  data() {
    return {
      codedata: {
        num: "",
        addr: "",
        beizhu: "",
        showpassword: "",
        check: [1],
        pks:''
      },
      value1: 1,
      min: 0,
      max: 0,
      step: 0,
      pawd: "",
      show1: false,
      num: 0,
      skey: "",
      padtxt:this.$t('code.rupassword'),
      password: "",
      headerNavData: {
        left: true,
        title: this.$t('code.zhuanzhang'),
        right: true,
        bj: "#2c94ed",
        addbooklist: true,
        share: false
      },
      gas: 200000,
      friendlist:[],
      showaddresBookbool:false,
      search:''
    };
  },
  created() {
    this.closeloading();
    this.codedata.addr = this.$route.query.scan;
    this.codedata.pks = getCookie("publickey")
    this.headerNavData.title ="(" + this.$route.query.name + ") " + this.$t("code.zhuanzhang");
    window.ToZhuanZhang = ToZhuanZhang;
    this.get_gas_price();
    this.getFriend();
  },
  methods: {
    //搜索
    cancelHandler(){
      this.search=''
    },
    //显示地址簿
    showaddresBook(){
      this.showaddresBookbool = true
    },
    goaddbook(){
      this.$router.push({path:'/addresbook'})
    },
    get_gas_price() {
      var that = this;
      this.axios({
        url: this.Url + "EthCommond.ashx",
        method: "post",
        params: { cmd: "gasprice" }
      }).then(res => {
        if (res.data.success) {
          // //  var gasprice=(GetGasPrice(1,res.data.data));
          // var gasprice = res.data.data;
          // // var totalGasPrice =  parseFloat(gasprice) * that.gas
          // var totalGasPrice = gasprice * that.gas;
          // // that.min =parseFloat((parseFloat(totalGasPrice).toFixed(8)));
          // that.min = totalGasPrice;
          // // that.max = parseFloat((parseFloat(this.min*10).toFixed(8)));
          // that.max = that.min * 10;
          // that.step = that.max / 20;
          // // that.step =parseFloat((parseFloat((this.max - this.min)/20).toFixed(8)));
          // //console.log(that.min,that.max)
          // that.num = totalGasPrice;
          // // that.num = this.bignum(totalGasPrice)


          var gasprice = res.data.data*10000;
          var totalGasPrice = gasprice * that.gas;
          that.min = totalGasPrice;
          that.max = that.min * 10;
          that.step = that.max / 20;
          that.num = totalGasPrice;
        }
      });
    },
    //吊起扫一扫
    scan() {
      Setvuefghfvmdfgdfgdfg(this);
      window.QRUtil.startScanQR("2", "ToZhuanZhang");
    },
    //获取好友地址列表
    getFriend() {
      var that = this;
      this.axios({
        url: this.Url + "AddressBook.ashx",
        method: "post",
        params: {
          req: 4,
          collector: getCookie("publickey"),
          pageindex: this.page,
          pagecount: 1000
        }
      }).then(res => {
        that.friendlist = res.data.data;
      });
    },
    getItem(item) {
      this.codedata.addr = item.Target;
      this.showaddresBookbool = false;
    },
    // 转账交易
    trade() {
      var that = this;
      this.show1 = false;
      this.openloading("转账中...");
      var name = this.$route.query.name;
      var codelist = JSON.parse(getCookie("codelist"));
      var publickey = getCookie("publickey");
      var contract = this.$route.query.contractAddress;
      for (var i = 0; i < codelist.length; i++) {
        if (publickey == codelist[i].publickey) {
          var keystore = codelist[i].keystore;
          setTimeout(function(){
            getPrivateFun(keystore, that.pawd, function(skey) {
            if (!skey || skey.length != 66) {
              that.closeloading();
              that.padtxt = that.$t('data.Passworderror');
              setTimeout(function() {
                that.show1 = true;
              },1000);
              return;
            }
            var num = that.codedata.num;
            var gasprice = (that.num / (that.gas * 10000)).toFixed(18);
            var numprice = GetGasPrice(0, gasprice);
            var you = that.codedata.addr;
            // 以太坊交易
            MysendRawTX(
              name,contract,publickey,you,skey,num,that.gas,numprice,that.codedata.beizhu,function(data) {
                if (!data.success) {
                  that.padtxt = data.msg;
                  return;
                }
                that.axios({
                    url: that.Url + "ETHCommond.ashx",
                    method: "post",
                    headers: {
                      "Content-Type": "application/x-www-form-urlencoded"
                    },
                    params: {
                      cmd: "send_raw_transaction",
                      publickey: getCookie("publickey"),
                      tx: data.data,
                      value: num,
                      symbol: name,
                      remark: that.codedata.beizhu,
                      from: publickey,
                      to: you,
                      nonce:data.nonce
                    }
                  }).then(res => {
                    that.closeloading();
                    if (res.data.success) {
                      that.Toast(that.$t('data.Transfersuccess'));
                      setTimeout(function() {
                        that.$router.replace({ path: "/tradelist" });
                      }, 2000);
                    } else {
                      that.Toast(res.data.msg);
                    }
                  });
              }
            )
          })
          },100)
        }
      }
    },
    showpwd() {
      if (this.codedata.addr == "" || this.codedata.addr == undefined) {
        this.Toast(this.$t('data.addresstransferred'));
        return;
      }
      if (!isAddressto(this.codedata.addr)) {
        this.Toast(this.$t('data.transferaddressincorrect'));
        return;
      }
      if (this.codedata.num == "") {
        this.Toast(this.$t('data.transferamount'));
        return;
      }
      this.show1 = true;
    },
    gowritezjc() {
      this.show1 = false;
      this.$router.push({ path: "/writezjc" });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong {
  padding-top: 0.9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC",
    "Helvetica Neue", STHeiti, "Microsoft Yahei", Arial, Tahoma, Simsun,
    sans-serif;
}
.yd-cell-item {
  border-bottom: 1px solid #f4f4f4;
}
.yd-checkbox-text {
  display: none;
}
.reda {
  padding: 0 0 0 0.28rem;
  font-size: 0.26rem;
  color: #666;
}
.reda span {
  color: #278eed;
}
.dig {
  background: #fff;
  padding: 0.4rem;
  text-align: center;
}
.ttl {
  font-size: 0.3rem;
  color: #278eed;
}
.dig input {
  width: 100%;
  height: 0.7rem;
  line-height: 0.7rem;
  padding: 0 0.12rem;
  background: #f4f4f4;
  border: none;
  border-radius: 5px;
  color: #999;
  font-size: 0.28rem;
  margin: 0.3rem 0;
}
.dig_bt button {
  margin: 0 0.3rem;
  padding: 0 0.5rem;
}
.dig_bt button:nth-child(1) {
  background: #c20919;
}
.dig_bt button:nth-child(2) {
  background: #278eed;
}
.btn_c {
  margin: 1.35rem 0 0.15rem;
  height: 0.7rem;
  line-height: 0.7rem;
  text-align: center;
  width: 100%;
  border: none;
  background: #278eed;
  font-size: 0.28rem;
  color: #fff;
  border-radius: 5px;
}
.btn_c:active {
  background: #237bcc;
}
.createf {
  background: #f4f4f4;
  padding: 0.2rem 0.35rem;
  font-size: 0.26rem;
  color: #278eed;
}
.touxi img {
  width: 0.5rem;
}
.block {
  padding: 0 0.3rem;
}
.qml {
  text-align: center;
  margin-top: 0.2rem;
}
.qml .man {
  font-size: 0.28rem;
  color: #666;
  text-align: left;
}
.qml .kuai {
  font-size: 0.28rem;
  color: #666;
  text-align: right;
}
.kgf {
  font-size: 0.25rem;
  color: #888;
}
.ttle {
  font-size: 0.28rem;
  color: #888;
  margin-bottom: 0.15rem;
}
.ttle img {
  width: 0.4rem;
}
.select_p {
  text-align: center;
  border-bottom: 1px solid #f7f7f7;
  padding: 12px 0;
  font-size: 0.3rem;
}
.select_p:last-child{
  border-bottom: none;
}
.select_p——ttl{
  background: rgb(44, 148, 237,.1);
  color:#2c94ed;
}
.addressbooklist{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: #f4f4f4;
}
.mysearch12{
  position: fixed;
  top: .9rem;
  left: 0;
  width: 100%;
  z-index: 99;
}
.tade_item{
  background: #fff;
  margin-bottom: .35rem;
}
.trade_time{
  height: .8rem;
  line-height: .8rem;
  padding-left: .3rem;
  font-size: .28rem;
  background: #f4f4f4;
}
.trade_info{
  padding: .35rem .3rem .1rem .3rem;
}
.trade_addr .left_d{
  float: left;
  font-size: .3rem;
  color: #333;
}
.trade_addr p{
  float: right;
  font-size: .32rem;
  text-align: right;
}
.trade_dabao{
  padding: .1rem .3rem .35rem .3rem
}
.dresslist{
  padding-top: 1rem;
}
.succorfail span:nth-child(1){
  font-size: .25rem;
  color: #666;
  display: block;
  float: left;
}
.succorfail span:nth-child(2){
  font-size: .25rem;
  color: #999;
  display: block;
  float: right;
  background: url('../assets/images/wc.png') no-repeat center center;
  background-size: .35rem .35rem;
  width: .35rem;
  height: .35rem;
}
.choseact{
  background: url('../assets/images/xz.png') no-repeat center center!important;
  background-size: .35rem .35rem!important;
}
</style>
<style>
.yingyong .yd-popup-content {
  background: #f7f7f7;
}
</style>



